import React from 'react';
import NombreUsuario from './NombreUsuario';
import Avatar from './Avatar';

const user = {
    nombre: "Alexis",
    apellido: "Carreras"
}

const Header = () => {
    return (<header className="encabezado">
        <div>
            <h1>ComIT</h1>
            <NombreUsuario user={user} className="user"/>
            <Avatar/>
        </div>
    </header>
    )
}

export default Header;