import React from 'react';

class Reloj extends React.Component {
    constructor(props){
        super(props);
        this.state = {date: new Date()}
    }

    componentDidMount(){
        this.timerID = setInterval(() => this.clock(), 1000)
    }

    clock(){
        this.setState({date: new Date()}) 
    }
 
    render(){
        return(
            <div>
                <h4 className="hora">La hora es: {this.state.date.toLocaleTimeString()}</h4>
            </div>
        )
    }
}

const Tablehead = () => {
    return(
        <tr className="tablehead">
            <th>Name</th>
            <th>Job</th>
            <th>Remove</th>
        </tr>
    )
}

const Deletebutton = () => {
    return(
        <button className="button">Delete</button>
    )
}

const Tablebody = () => {
    return(
        <div className="tablebody">
        <tr className="fila1">
            <td>Alexis</td>
            <td>Programador</td>
            <td><Deletebutton/></td>
        </tr>
        <hr/>
        <tr className="fila2">
            <td>Alexis</td>
            <td>Programador</td>
            <td><Deletebutton/></td>
        </tr>
        <hr/>
        <tr className="fila2">
            <td>Alexis</td>
            <td>Programador</td>
            <td><Deletebutton/></td>
        </tr>
        </div>
    )
}

const Table = () => {
    return(
        <table className="tabla">
            <Tablehead/>
            <Tablebody/>
        </table>
    )
}

const Section = () => {
    return( <section className="seccion">
        <Reloj/>
        <Table/>
        <p>Es una librería de Javascript para crear interfaces de usuario
        (UI) en el front-end.</p>
        <p>React crea componentes , que son elementos HTML
        personalizados y reutilizables, para crear interfaces de usuario
        de manera rápida y eficiente. Además optimiza la forma en
        que se almacenan y manejan los datos.</p>
    </section>
    )
} 

export default Section;